<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <h1>{{ isActive }}</h1>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="addRolesVisible = true"
            ></el-button
          >
          <div class="phoneNumber">
            <div
              v-for="(data, index) in keepList"
              :key="data"
              style="height:80px;"
              class="content"
            >
              <div class="person">
                <span class="index">{{ keepList.length - index }}</span>
              </div>
              <div style="margin-left:30px;">
                :{{ index }}
                i
              </div>
            </div>
            <!-- <div style="height: 300px;">
              <el-steps
                direction="vertical"
                :active="codeIndex"
                process-status="success"
                :simple="true"
              >
                <el-step title=" 1"></el-step>
                <el-step title=" 2"></el-step>
                <el-step
                  title=" 3"
                  description=""
                ></el-step>
              </el-steps>
            </div> -->
          </div>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="rolelist" border stripe>
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']"
              v-for="(item1, i1) in scope.row.children"
              :key="item1.id"
            >
              <!--  -->
              <el-col :span="5">
                <el-tag
                  closable
                  @close="removeRightById(scope.row, item1.id)"
                  >{{ item1.authName }}</el-tag
                >
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <!--  for   -->
                <el-row
                  :class="[i2 === 0 ? '' : 'bdtop', 'vcenter']"
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRightById(scope.row, item2.id)"
                      >{{ item2.authName }}</el-tag
                    >
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag
                      type="warning"
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      closable
                      @close="removeRightById(scope.row, item3.id)"
                      >{{ item3.authName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="300px">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="editRolesBtn(scope.row)"
              ></el-button
            >
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="removeRolesBtn(scope.row)"
              ></el-button
            >
            <el-button
              size="mini"
              type="warning"
              icon="el-icon-setting"
              @click="showSetRightDialog(scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <el-dialog
      title=""
      :visible.sync="setRightDialogVisible"
      width="50%"
      @close="setRightDialogClosed"
    >
      <!--  -->
      <el-tree
        :data="rightslist"
        :props="treeProps"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="defKeys"
        ref="treeRef"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false"> </el-button>
        <el-button type="primary" @click="allotRights"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addRolesVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="80px"
      >
        <el-form-item label="" prop="roleName">
          <el-input v-model="addForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="addForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addRolesVisible = false"> </el-button>
        <el-button type="primary" @click="addRolesInfo"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editRolesVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="80px"
      >
        <el-form-item label="" prop="roleName">
          <el-input v-model="editForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="editForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editRolesVisible = false"> </el-button>
        <el-button type="primary" @click="editRolesInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import {
  getRoles,
  deleteRole,
  getAllroles,
  allotRight_Api,
  addRoles,
  deleteRoles,
  editRolesSubmit
} from '@/api/roles.js'
export default {
  data() {
    return {
      isActive: '01',
      date: '20210906',
      // 
      rolelist: [],
      setRightDialogVisible: false,
      rightslist: [],
      // 
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      // 
      defKeys: [],
      // id
      roleId: '',
      editId: '',
      // 
      keepList: [],
      codeIndex: 1,
      addRolesVisible: false,
      addForm: {
        roleName: '',
        roleDesc: ''
      }, //
      addFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 10, message: ' 2  10 ', trigger: 'blur' }
        ],
        roleDesc: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 5,
            max: 100,
            message: ' 5  100 ',
            trigger: 'blur'
          }
        ]
      },
      editRolesVisible: false, //
      editForm: {
        roleName: '',
        roleDesc: ''
      }, //
      editFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 10, message: ' 2  10 ', trigger: 'blur' }
        ],
        roleDesc: [
          { required: false, message: '', trigger: 'blur' },
          {
            min: 5,
            max: 100,
            message: ' 5  100 ',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  mounted() {
    this.getRolesList()
    this.isActive = '02'
  },
  computed: {},
  methods: {
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // 
    editRolesInfo() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await editRolesSubmit(this.editId, this.editForm)
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }

        this.$message.success('')
        this.getRolesList()
        this.editRolesVisible = false
      })
    },
    // 
    editRolesBtn(role) {
      console.log('555555', role)
      this.editId = role.id
      this.editRolesVisible = true
      this.editForm.roleName = role.roleName
      this.editForm.roleDesc = role.roleDesc
    },
    // 
    removeRolesBtn(role) {
      var roleID = role.id
      deleteRoles(roleID).then(res => {
        if (res.data.meta.status !== 200) {
          return this.$message.error('')
        } else {
          this.$message.success('')
          this.getRolesList()
        }
      })
    },
    // 
    addRolesInfo() {
      this.$refs.addFormRef.validate(valid => {
        if (!valid) return
        // this.$http.post('users', this.addRuleForm)
        addRoles(this.addForm).then(res => {
          console.log('res', res.data)
          if (res.data.meta.status !== 201)
            return this.$message.error('')
          // 
          this.addRolesVisible = false
          // 
          this.getRolesList()
          this.$message.success('')
        })
      })
    },
    showSetRightDialog(role) {
      console.log('role', role)
      this.roleId = role.id
      getAllroles().then(res => {
        if (res.data.meta.status !== 200) {
          return this.$message.error('')
        }
        this.rightslist = res.data.data

        this.setRightDialogVisible = true
        this.getLeafKeys(role, this.defKeys)
      })
    },
    // 
    getRolesList() {
      getRoles()
        .then(res => {
          console.log(res)
          if (res.data.meta.status !== 200) {
            return this.$message.error('')
          }
          this.rolelist = res.data.data
        })
        .catch(err => {
          return this.$message.error('' + err)
        })
    },
    async removeRightById(role, rightId) {
      console.log('')
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => {
        err
      })
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      deleteRole(role.id, rightId).then(res => {
        if (res.data.meta.status !== 200) {
          return this.$message.error('')
        } else {
          role.children = res.data.data
        }
      })

      // const { data: res } = await deleteRole(role.id,rightId)
    },
    // id defKeys 
    getLeafKeys(node, arr) {
      //  node  children 
      if (!node.children) {
        return arr.push(node.id)
      }

      node.children.forEach(item => this.getLeafKeys(item, arr))
    },
    // 
    setRightDialogClosed() {
      this.defKeys = []
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    async allotRights() {
      console.log('555555555', this.$refs.treeRef.getCheckedKeys())
      console.log('6666666', this.$refs.treeRef.getHalfCheckedKeys())
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      console.log(' this.roleId', this.roleId)
      const idStr = keys.join(',') //
      const { data: res } = await allotRight_Api(this.roleId, idStr)

      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.$message.success('')
      this.getRolesList()
      this.setRightDialogVisible = false
    }
  },
  watch: {
    isActive: {
      handler(newVal, oldVal) {
        console.log('newVal', newVal)
        console.log('oldVal', oldVal)
        this.isActive = '03'
      },
      immediate: true,
      deep: true
    }
  }
}
</script>
<style scoped lang="less">
.el-tag {
  margin: 8px;
}
.bdtop {
  border-top: 1px solid #eee;
}

.bdbottom {
  border-bottom: 1px solid #eee;
}

.vcenter {
  display: flex;
  align-items: center;
}
/*
*
*/
.phoneNumber {
  margin-top: 20px;
  height: 90%;
  position: relative;
  &::before {
    content: ''; //\260E
    font-size: 15px;
    margin-right: 5px;
    width: 1px;
    top: 20px;
    bottom: 84px;
    background: #e5e5e5;
    left: 10px;
    position: absolute;
  }
}
.person {
  font-size: 16px;
  color: #999;
  display: flex;
  flex-basis: auto;
  justify-content: space-between;
  position: relative;
  .index {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    width: 20px;
    height: 20px;
    border-radius: 20px;
    background: blue;
  }
}
.content {
  display: flex;
  flex-basis: auto;
  // justify-content: space-between;
}
</style>
