/* *@Description: *@ClassAuthor: Wang Xiao Gang *@Date: 2021-08-06 16:07:41 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getUserList"
          >
            <template #append>
              <!-- slot="append" -->
              <el-button icon="el-icon-search" @click="getUserList"></el-button>
            </template>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button class="button" type="primary" @click="addUsers"
            ></el-button
          >
        </el-col>
      </el-row>

      <!--  -->
      <el-table
        :data="userlist"
        border
        stripe
        :header-cell-style="{ background: '#eef1f6', color: '#606266' }"
        :sortable="true"
        @sort-change="sortState"
        @selection-change="handleSelectionChange"
        style="width: 100%"
        :span-method="objectSpanMethod"
        @current-change="onCurrentRow"
      >
        <!-- <el-table-column type="selection"></el-table-column> -->

        <el-table-column prop="username" align="center" label="">
        </el-table-column>
        <el-table-column label="" prop="email"></el-table-column>
        <el-table-column prop="mobile" align="center" label="">
        </el-table-column>
        <el-table-column label="" prop="role_name"></el-table-column>
        <el-table-column
          label=""
          sortable="custom"
          :sort-orders="['ascending', 'descending']"
          prop="mg_state"
        >
          <template #default="scope">
            <el-switch
              v-model="scope.row.mg_state"
              @change="handleSwitch(scope.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" width="190px">
          <template #default="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="alterUserList(scope.row.id)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteUserList(scope.row)"
            ></el-button>
            <el-tooltip
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="info"
                icon="el-icon-setting"
                size="mini"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 4, 6, 8, 20]"
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      width="50%"
      :visible.sync="dialogVisible"
      @close="addDialogClosed"
    >
      <el-form
        ref="addFormRef"
        :model="addRuleForm"
        label-width="100px"
        class="demo-ruleForm"
        :rules="addRule"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="addRuleForm.username"></el-input>
        </el-form-item>

        <el-form-item label="" prop="password">
          <el-input v-model="addRuleForm.password" type="password"></el-input>
        </el-form-item>

        <el-form-item label="" prop="email">
          <el-input v-model="addRuleForm.email"></el-input>
        </el-form-item>

        <el-form-item label="" prop="mobile">
          <el-input v-model="addRuleForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="addSubmit"> </el-button>
        </span>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="70px"
      >
        <el-form-item label="">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import {
  getUser,
  handleSwitchBtn,
  deleteUser,
  addUser,
  editUser,
  alterUserApi,
} from '@/api/user.js'
export default {
  data() {
    // 
    var checkEmail = (rule, value, cb) => {
      // 
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
      if (regEmail.test(value)) {
        return cb()
      }
      cb(new Error(''))
    }
    // 
    var checkMobile = (rule, value, cb) => {
      // 
      const regMobile =
        /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/

      if (regMobile.test(value)) {
        return cb()
      }
      cb(new Error(''))
    }
    return {
      cities: [
        {
          value: 'Beijing',
          label: '',
        },
        {
          value: 'Shanghai',
          label: '',
        },
        {
          value: 'Nanjing',
          label: '',
        },
        {
          value: 'Chengdu',
          label: '',
        },
        {
          value: 'Shenzhen',
          label: '',
        },
        {
          value: 'Guangzhou',
          label: '',
        },
      ],
      selectValue: '',
      // 
      queryInfo: {
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 1,
      },
      value2: '', //
      // 
      total: 0,
      // 
      userlist: [],
      userDate: '',
      // 
      dialogVisible: false,
      // 
      addRuleForm: {
        username: '', //
        password: '', //
        email: '', //
        mobile: '', //
      },
      // 
      multipleSelection: [],
      addRule: {
        // 
        username: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 2,
            max: 10,
            message: ' 2  10 ',
            trigger: 'blur',
          },
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 6,
            max: 18,
            message: ' 6  18 ',
            trigger: 'blur',
          },
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' },
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' },
        ],
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      // 
      editFormRules: {
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' },
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' },
        ],
      },
    }
  },
  created() {
    this.getUserList()
  },
  mounted() {
    this.userDate = '20210818'
    console.log('route', this.$router.currentRoute.fullPath)
    console.log('hahahahahha', this.userDate.indexOf('2021') !== -1)
    var a = 3,
      b = 4,
      c = false
    if ((5 > a && 5 > b) || c == true) {
      console.log('342""""')
    }
  },
  methods: {
    sortState({ prop, order }) {
      console.log('prop', { prop })
      console.log('order', { order })
      this.userlist.sort(this.compare(prop, order))
      // this.userlist.sort(function(a, b) {
      //   // debugger
      //   console.log('a', a)
      //   console.log('b', b)
      // })
    },
    compare(propertyName, sort) {
      return function (obj1, obj2) {
        console.log('obj1', obj1)
        console.log('obj2', obj2)
        var value1 = obj1[propertyName]
        var value2 = obj2[propertyName]

        console.log('wangxiaogang ')
        if (typeof value1 === 'string' && typeof value2 === 'string') {
          const res = value1.localeCompare(value2, 'zh')
          return sort === 'ascending' ? res : -res
        } else {
          if (value1 <= value2) {
            return sort === 'ascending' ? -1 : 1
          } else if (value1 > value2) {
            return sort === 'ascending' ? 1 : -1
          }
        }
      }
    },
    selectclick() {
      console.log('222', this.selectValue)
    },
    // 
    async getUserList() {
      const { data: res } = await getUser(this.queryInfo)
      // this.$http.get('users', {
      //   params: this.queryInfo
      // })
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.userlist = res.data.users
      this.total = res.data.total
      this.$store.commit('setdetailList', res.data.users)
    },
    // 
    async handleSwitch(data) {
      console.log('888', data)
      const { data: res } = await handleSwitchBtn(data)
      // this.$http.put(
      //   `users/${data.id}/state/${data.mg_state}`
      // )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
    },
    //  pagesize 
    handleSizeChange(pagesize) {
      console.log('88888', pagesize)
      this.queryInfo.pagesize = pagesize
      this.getUserList()
    },
    //   
    handleCurrentChange(value) {
      console.log('99999', value)
      this.queryInfo.pagenum = value
      this.getUserList()
    },
    // 
    async alterUserList(id) {
      // console.log(id)
      const { data: res } = await alterUserApi(id)
      // this.$http.get('users/' + id)

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      console.log('res.data', res.data)
      this.editForm = res.data
      this.editDialogVisible = true
    },
    // 
    async deleteUserList(datas) {
      console.log('', datas)
      const { data: res } = await deleteUser(datas)
      // this.$http.delete(`users/${datas.id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getUserList()
    },
    addUsers() {
      console.log('180')
      this.dialogVisible = true
    },
    // 
    addSubmit() {
      // this.$refs.addFormRef.validate(async valid => {
      //   if (!valid) return
      //   var { data: res } = await this.$http.post('users', this.addRuleForm)
      //   if (res.meta.status !== 201) return this.$message.error('')
      //   // 
      //   this.dialogVisible = false
      //   // 
      //   this.getUserList()
      //   this.$message.success('')
      // })
      console.log(this.$refs.addFormRef.model)
      this.$refs.addFormRef.validate((valid) => {
        if (!valid) return
        // this.$http.post('users', this.addRuleForm)
        addUser(this.addRuleForm).then((res) => {
          console.log('res', res.data)
          if (res.data.meta.status !== 201)
            return this.$message.error('')
          // 
          this.dialogVisible = false
          // 
          this.getUserList()
          this.$message.success('')
        })
      })
    },
    // 
    editUserInfo() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return
        // 
        const { data: res } = await editUser(this.editForm)
        // this.$http.put(
        //   'users/' + this.editForm.id,
        //   {
        //     email: this.editForm.email,
        //     mobile: this.editForm.mobile
        //   }
        // )

        if (res.meta.status !== 200) {
          return this.$message.error('')
        }

        // 
        this.editDialogVisible = false
        // 
        this.getUserList()
        // 
        this.$message.success('')
      })
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // 
    addDialogClosed() {
      console.log('1111', 1111)
      this.$refs.addFormRef.resetFields()
    },
    //
    handleSelectionChange(val) {
      console.log('bbb', val)
      this.multipleSelection = val
    },
    onCurrentRow(val) {
      console.log('cccc', val)
    },
    objectSpanMethod({ row, column, rowIndex, columnIndex }) {
      // rowcolumnrowIndexcolumnIndex,
      // if (columnIndex === 0) {
      //   if (rowIndex % 3 === 0) {
      //     return {
      //       rowspan: 3,
      //       colspan: 1
      //     }
      //   } else {
      //     return {
      //       rowspan: 0,
      //       colspan: 0
      //     }
      //   }
      // }
    },
  },
}
</script>
<style scoped lang="less">
/*  */
/deep/ .el-input__prefix {
  right: 0px;
  text-align: right;
}
/*  */
// ::v-deep .el-input__prefix {
//   right: 0px;
//   text-align: right;
